import React from "react";
import "./ManualProjectCard.css";

// This is the fully updated component
export default function ManualProjectCard({ project, theme }) {
  const openUrlInNewTab = (url) => {
    if (!url || url === "#") return;
    const newWindow = window.open(url, "_blank", "noopener,noreferrer");
    if (newWindow) newWindow.opener = null;
  };

  return (
    <div
      className="manual-project-card"
      style={{
        border: `1px solid ${theme.secondaryText}`,
        cursor: project.url && project.url !== '#' ? 'pointer' : 'default'
      }}
      onClick={() => openUrlInNewTab(project.url)}
    >
      <div className="manual-project-text">
        <h3 className="manual-project-title" style={{ color: theme.text }}>
          {project.title}
        </h3>
        <p className="manual-project-description" style={{ color: theme.secondaryText }}>
          {project.description}
        </p>
        
        {(project.role || project.duration) && (
          <div className="project-meta-info">
            {project.role && (
              <div className="meta-item">
                <span className="meta-title" style={{ color: theme.text }}>Role:</span>
                <span className="meta-value" style={{ color: theme.secondaryText }}>{project.role}</span>
              </div>
            )}
            {project.duration && (
              <div className="meta-item">
                <span className="meta-title" style={{ color: theme.text }}>Duration:</span>
                <span className="meta-value" style={{ color: theme.secondaryText }}>{project.duration}</span>
              </div>
            )}
          </div>
        )}

        <div className="manual-project-technologies">
          {project.technologies.map((tech, i) => {
            return (
              <span key={i} className="tech-badge" style={{ color: theme.text, backgroundColor: theme.body }}>
                {tech}
              </span>
            );
          })}
        </div>
      </div>
    </div>
  );
}